import react from 'react'
import { connect } from 'react-redux';
import { Field, reduxForm } from 'redux-form'
import { loginTC } from '../../Redux/auth-Reduser';
import { required } from '../../Validate/Validator';
import Input from '../redux-form/Input';

const LoginForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                {props.error }
            </div>
            <div>
                <Field component={Input} validate={[required]} name='email' placeholder='login' />
            </div>
            <div>
                <Field component={Input} validate={[required]} name='password' placeholder='password' />
            </div>
            <div>
                <Field component='input' name='rememberMe' type='checkbox' />rememberMe
            </div>
            <div>
                <button>LogIn</button>
            </div>
        </form>


    )
}
const LoginReduxForm = reduxForm({ form: 'login' })(LoginForm);
const Login = (props) => {
    const onSubmit = (dataForm) => {
        props.loginTC(dataForm.email, dataForm.password, dataForm.rememberMe)
    }
    return (
        <div>
            <h1>LogIn</h1>
            <LoginReduxForm onSubmit={onSubmit} />
        </div>
    )
}

export default connect(null,{loginTC})(Login)